<template>
    <div class="v-feed border-bottom-1px" :class="[classes]" @click="goDetails">
        <div @click="handleClick"><div class="v-feed__img" :class="[feedClass]"><img :src="imgUrl" ></div><i v-if="msgNum == 0" class="v-feed__num" :class="[{mix: msgNum == 0}]"></i></div>
        <div class="v-feed__con">
            <div class="v-feed__con-info">
              <!--列表title-->
                <div class="v-feed__tit" :class="{'addWidth': receiptInfo.flag === '-1'}" v-text="title" v-if="title"></div>
              <!--详情title-->
                <div class="v-feed__details" :class="{'clearWidth': receiptInfo.flag === '-1'}" v-text="titleDetails" v-if="titleDetails"></div>
                <div class="v-feed__sex icon" :class="[{'icon-male': sex == '1'}, {'icon-female': sex == '2'}]" v-if="sex"></div>
                <div class="v-feed__age" v-if="age" v-text="age"></div>
                <div class="v-feed__desc" v-text="desc" v-if="desc"></div>
              <!--回执信息-->
                <div class="v-feed-info" v-if="receiptInfo.flag" :class="[{'three-word': receiptInfo.flag == '0'}, {'four-word': receiptInfo.flag == '1'} ,{'hide' :receiptInfo.flag == '-1'}]">
                  <img :src="receiptInfo.receiptImg">
                </div>
              <!--限时回执时间-->
              <span class="sp-time-info" v-if="time.time" :class="{'gray': receiptInfo.setTimeColor}">{{time.time | time}}</span>
            </div>
            <div class="v-feed__sub">
              <!--详情信息-->
              <span class="sp-info" v-text="subtitle"></span>
              <!--联系Ta-->
              <span class="sp-msg" v-show="showRelation" @click="openToon"></span>
            </div>
            <div class="v-feed__sub item-info" v-text="itemInfo" v-show="itemInfo"></div>
        </div>
    </div>
</template>
<script type="text/ecmascript-6">
    import utils from '../js/utils/utils';
    /**
     * feed组件
     *              -- Author by Dio Zhu. on 2017.2.8
     */
    export default {
        name: 'v-feed',

        props: {
            myFeedId: String, // 我的feedId
            friendFeedId: String, // 发布人feedIdv-feed__tit
            id: Number,         // 当前id
            noType: Number,     // 当前类型  0 通知 1 作业 2 成绩
            feedId: String,     // 控制显示头像形状
            imgUrl: String,     // 头像地址
            msgNum: Number,     // 头像右上角标识数字
            title: String,      // 用户名
            titleDetails: String, // 详情title
            subtitle: String,   // 推广语
            sex: String,        // 性别: 0未知，1男，2女
            age: String,        // 年龄
            desc: String,        // 右上角备注，时间（13:00）或者距离（1.1km以内）
            classes: String,     // 附加class
            iconFunc: Function,  // 点击头像的回调函数
            receiptInfo: Object,  //回执信息
            receiptImg: String,   //回执信息图片
            itemInfo: String,     //省略信息
            time: Object,  // 限时时间
            showRelation: Boolean, // 是否显示联系Ta
            currentType: Number, // 判断当前点击类别（通知/接收）
            timeColor: Boolean, // 控制时间颜色
            currentReceipt: Number // 当前点击是需要回执/不需要回执
        },

        data () {
            return {
            };
        },

        computed: {
            feedImg () {
                // return utils.getThumbnail(this.imgUrl);
            },
            /**
             * 根据feedid判断来源, 显示不同形式头像.
             * o、s方的，其他圆的, g是群组,六边形, 暂时不考虑.
             *              -- Author by Dio Zhu. on 2016.12.2
             */
            feedClass () {
                let rtn = '';
                if (/^o_/.test(this.feedId) || /^s_/.test(this.feedId)) {
                    rtn = 'square';
                } else if (/^g_/.test(this.feedId)) {
                    rtn = 'hexagon';
                } else {
                    rtn = 'circle';
                }
                return rtn;
            }
        },

        created () {
//            this.$logger.log('v-feed created...');
        },

        methods: {
            handleClick (e) {
                if (this.iconFunc && typeof this.iconFunc === 'function') {
                    e.stopPropagation();
                    e.preventDefault();
                    this.iconFunc();
                    this.$toast({message: '您是不是想去个人中心？', duration: 1000, position: 'bottom'});
                }
            },
          /**
           * 根据不同类别 跳转不同路由
           */
          goDetails () {
              if (this.currentType === 0) {
                this.$router.push({
                  path:'/informDetails',
                  query: {
                    noId: this.id,
                    currentReceipt: this.currentReceipt,
                    noType: this.noType
                  }
                });
              } else if (this.currentType === 1 && this.noType !== 2 ) {
                this.$router.push({
                  path:'/receiveDetails',
                  query: {
                    noId: this.id
                  }
                });
              } else if (this.currentType === 1 && this.noType == 2 ) {
                this.$router.push({
                  path:'/scoreDetail',
                  query: {
                    noId: this.id
                  }
                });
              }
          },
          /**
           * 调toon协议 发起聊天
           */
          openToon () {
            // 跳转聊天界面
            //let obj = {myFeedId: this.myFeedId, friendFeedId: this.friendFeedId, backToPrev: 1};
            //window.location.href = "toon://chat?params=" + JSON.stringify(obj);
            toongine.chat.openChat({
              params: {
                'myFeedId' : this.myFeedId,
                'friendFeedId' : this.friendFeedId,
                'backToPrev' : '0'
              },
              callback: res => {
                console.log("res: " + res.code);
              }
            });
          }
        }
    };
</script>
<style rel="stylesheet/scss" lang="scss">
    @import "../scss/variables";
    @import "../scss/mixins";

    .v-feed {
      padding: 18px 14px 15px 0;
      /*min-height: 91px;*/
        /*height: pxTorem(91px);*/
        /*background: #fff;*/
        /*border-bottom: #e1e1e1 1px solid;!*no*!*/
        /*background: #DDDEE3;*/
        position: relative;

        //        @include box_flex;
        //        @include align_items(center);
        display: flex;
        /*align-items: center;*/

        > div {
            position: relative;
            /*@include flex_grow(0);
            @include flex_shrink(0);
            @include flex_basis(auto);*/
            flex: 0 0 auto;

            &:last-child {
                /*@include flex_grow(1);
                @include flex_shrink(1);*/
                flex: 1 1 auto;
            }
        }
    }

    .v-feed__img {
        width: pxTorem(54px);
        height: pxTorem(54px);
        margin-left: pxTorem(10px);
        margin-right: pxTorem(10px);
        position: relative;
        border: 0;

        img {
            width: 100%;
        }

        &.circle {
            -webkit-border-radius: 50%;
            -moz-border-radius: 50%;
            border-radius: 50%;
            overflow: hidden;
        }
        &.hexagon {
            height:pxTorem(46px);

            &:before {
                content: "";
                width: 0;
                height: 0;
                position: absolute;
                left: 0;
                top: 0;
                border-right: pxTorem(12px) solid transparent;
                border-top: pxTorem(23px) solid #FFF;
                border-bottom: pxTorem(23px) solid #FFF;
            }
            &:after {
                content: "";
                width: 0;
                height: 0;
                position: absolute;
                right: 0;
                top: 0;
                border-left: pxTorem(12px) solid transparent;
                border-top: pxTorem(23px) solid #FFF;
                border-bottom: pxTorem(23px) solid #FFF;
            }
        }
    }

    .small {
        height: pxTorem(52px);

        .v-feed__img {
            width: pxTorem(32px);
            height: pxTorem(32px);
            margin-left: pxTorem(15px);
        }

        .v-feed__tit {
            font-size: pxTorem(17px);
        }
        .v-feed__sub {
            font-size: pxTorem(12px);
        }
    }

    .v-feed__num {  /* 头像右上角数字 */
        width: pxTorem(18px);
        height: pxTorem(18px);
        -webkit-border-radius: 50%;
        -moz-border-radius: 50%;
        border-radius: 50%;
        background: #FF3B2F;
        position: absolute;
        right:pxTorem(6px);
        top: pxTorem(-4px);
        font-size: pxTorem(12px);
        color: #FFF;
        line-height: pxTorem(18px);
        text-align: center;

        &.mix { /* 10以上不显示数字，只显示成红点儿 */
            width: pxTorem(10px);
            height: pxTorem(10px);
            color: transparent;
            right: pxTorem(8px);
            top: pxTorem(-2px);
        }
    }

    .v-feed__con {
        @include box_flex;
        //        @include flex-direction-column;
        /*margin-right: pxTorem(10px);*/
        flex-direction: column;
    }

    .v-feed__con-info {
      position: relative;
        @include box_flex;
        @include flex-direction-row;
        @include justify-content(space-between);

        > div:last-child {
            @include flex_grow(1);
            @include flex_shrink(1);
            @include flex_basis(auto);
        }
    }

    .v-feed__tit {
        font-size: pxTorem(17px);
        line-height: 1;
        color: #383838;
        width: 210px;
        overflow: hidden;
        text-overflow:ellipsis;
        white-space: nowrap;
    }
    .v-feed__tit.addWidth{
      width: 280px;
    }
    .v-feed__details{
      font-size: pxTorem(17px);
      line-height: 22px;
      color: #383838;
      width: 210px;
      word-wrap: break-word;
    }
    .v-feed__details.clearWidth{
      width: auto;
    }
    .v-feed__sex { /* 性别icon */
        width: pxTorem(12px);
        height: pxTorem(12px);
        margin: pxTorem(1px) pxTorem(5px);
        text-align: center;
        /*font-size: pxTorem(8px);*/
        font-size: pxTorem(12px);
        line-height: pxTorem(12px);
        color: #FF88B2;
        /*background: #FF88B2;*/
        -webkit-border-radius: pxTorem(2px);
        -moz-border-radius: pxTorem(2px);
        border-radius: pxTorem(2px);

        &.icon-male { /* 男 */
            /*background: #4392EC;*/
            color: #4392EC;
        }
    }
    .v-feed__age {/* 年龄 */
        font-size: pxTorem(10px);
        line-height: pxTorem(17px);
        color: #777E8C;
    }
    .v-feed__desc {
        text-align: right;
        font-size: pxTorem(12px);
        line-height: 1;
        color: #8E8E93;
    }
    .v-feed__sub {
        margin-top: pxTorem(10px);
        font-size: pxTorem(13px);
        line-height: 1;
        color: #9c9c9c;
      &.item-info{
        width: 280px;
        margin-top: pxTorem(11px);
        overflow: hidden;
        text-overflow:ellipsis;
        white-space: nowrap;
      }
      /*联系Ta icon*/
      .sp-msg{
        display: inline-block;
        margin-top: -1px;
        width: 55px;
        height: 20px;
        background: url("../assets/icon-msg.png") no-repeat;
        background-size: contain;
        vertical-align: middle;
      }
    }

    /*一共五中状态*/
    /*需回执、限时回执、已截止、无需回执、已回执*/
    .v-feed-info {
      position: absolute;
      top: 2px;
      right: 3px;
      &.three-word{
        width: 48px;
        height: 18px;
        img{
          width: 100%;
          height: 100%;
        }
      }
      &.four-word{
        width: 60px;
        height: 18px;
        img{
          width: 100%;
          height: 100%;
        }
      }
      &.hide{
        display: none;
      }
    }
    /*限时回执时间信息*/
    .sp-time-info{
      position: absolute;
      top: 26px;
      right: 3px;
      font-size: 11px;
      color: #FF5E2D;
      &.gray{
        color: #ACB3BF;
      }
    }


</style>



